import React from 'react'
import Link from 'gatsby-link'
import PageTransition from 'gatsby-plugin-page-transitions';
import background from '../images/background10.jpg';

const SchoolPage = () => (
  <PageTransition
    defaultStyle={{
      transition: 'right 500ms cubic-bezier(0.47, 0, 0.75, 0.72)',
      right: '0%',
      position: 'absolute',
      width: '100%',
    }}
    transitionStyles={{
      entering: { },
      entered: {  },
      exiting: { right: '100%' },
    }}
    transitionTime={500}
  >
    <div style={{
        backgroundImage: `url(${background})`,
        backgroundSize: 'cover',
        width: '100vw',
        height: '100vh',
        color: '#FFFFFF',
        display: 'flex',
        justifyContent: 'center',
      }}
    >
      <div style={{
          paddingTop: '120px',
          width: '960px',
          textAlign: 'center',
          display: 'flex',
          flexDirection: 'row'
        }}
      >

        <div style={{
            width: '48%',
            marginRight: '4%'
          }}
        >
          <div style={{
              backgroundColor: '#F2F2F2',
              height: '400px',
              borderTopRightRadius: '15px',
              borderTopLeftRadius: '15px',
            }}
          >

          </div>

          <div style={{
              backgroundColor: 'rgba(242,242,242, .2)',
              height: '200px',
              borderBottomRightRadius: '15px',
              borderBottomLeftRadius: '15px',
              padding: '15px'
            }}
          >
            <p>William Mason High School</p>
          </div>
        </div>

        <div style={{
            width: '48%',
          }}
        >
          <div style={{
              backgroundColor: '#F2F2F2',
              height: '400px',
              borderTopRightRadius: '15px',
              borderTopLeftRadius: '15px',
            }}
          >

          </div>

          <div style={{
              backgroundColor: 'rgba(242,242,242, .2)',
              height: '200px',
              borderBottomRightRadius: '15px',
              borderBottomLeftRadius: '15px',
              padding: '15px'
            }}
          >
            <p>University Of Dayton</p>
          </div>
        </div>


      </div>
    </div>
  </PageTransition>
)

export default SchoolPage



